import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import ReactHtmlParser from 'react-html-parser'
import moment from "moment";
import NotificationHeader from "./Components/NotificationHeader";
import { deleteNotification, getAllNotification } from "../../../actions/notificationAction";
import { togglePlanSection } from "../../../actions/show_modal_Action";
import { DELETE_NOTIFICATION, MARK_ALL_AS_READ } from "../../../reducer/keys";
import { TOAST_LOADING, onDeletePop } from "../../../utils";
import { CLOUDPATH_TAGGSHOP } from "../../../actions/api";


export default function Notification() {
  const [index,setIndex] = useState(0);
  const contentLength = 10;
  const dispatch = useDispatch();
  const [sortByNotification,setSortByNotification] = useState(true);
  const [sortByTime,setSortByTime] = useState(true);
  const { notificationList,notificationListLength } = useSelector((state) => state.notification);

  useEffect(()=>{
    getNotifications(index);
  },[]);

  const getNotifications = (pageIndex,direction='DESC',sortby='createdAt') =>{
    dispatch(getAllNotification({type: "all",synced: 0,created: "", page:pageIndex,perPage:contentLength,sortby:sortby,direction:direction},false));
    dispatch({type: MARK_ALL_AS_READ})
  }

  const incrementIndex = (index)=>{

    if(((index)*10)<notificationListLength){
      getNotifications(index);
      setIndex(prev=>prev+1);
    }
  }

  const decrementIndex = (index) =>{
    if(((index)*10)>=0){
      getNotifications(index);
      setIndex(prev=>prev-1);
      }
  }

  const getNotificationAction = (action, getNotificationUrl, message) => {
    if (action === 'init') {
        return <a href={`${getNotificationUrl}`} className={`notification_row my-3 pl-0`}>
            {ReactHtmlParser(message)}
        </a>
    } else if (action === 'popup') {
        return <a className={`notification_row my-3 pl-0`} onClick={() => dispatch(togglePlanSection(true))}>
            {ReactHtmlParser(message)}
        </a>
    } else if (action === 'ugc') {
        return <a href={getNotificationUrl} className={`notification_row my-3 pl-0`} target='_blank'>
            {ReactHtmlParser(message)}
        </a>
    }
    return <a href={`${getNotificationUrl}`} className={`notification_row my-3 pl-0`}>
        {ReactHtmlParser(message)}
    </a>
}

 const removeNotification = (id)=>{
  onDeletePop(()=>dispatch(deleteNotification(id)));
 }

 const sortNotificationById = (direction) =>{
  setSortByNotification(false);
  if(!sortByTime){
    setSortByTime(true);
  }
  getNotifications(index,direction,'message');
 }

 const sortNotificationByTimeStamp = (direction) =>{
  setSortByTime(false);
  if(!sortByNotification){
    setSortByNotification(true);
  }
  getNotifications(index,direction,'createdAt');
 }


  return (
    <div  className="d-flex flex-column-fluid main_content no_subheader all_notifications__">
    <div className="container">
      <div className="row">
        <div className="portlet-body col-md-12 mb-10">
          {notificationList.length > 0
                  ? 
                  <div className="table-responsive bg-white rounded-2 p-5">
            <table className="table table-hover table-light notification-heading">
              <thead>
                <tr className="uppercase">
                  <th></th>
                  <NotificationHeader sortFun={(direction)=>sortNotificationById(direction)} displayIcon={sortByNotification} name={'Notification'}/>
                  <NotificationHeader sortFun={(direction)=>sortNotificationByTimeStamp(direction)} displayIcon={sortByTime} name={'Created'}/>
                </tr>
              </thead>
              <tbody>
                {notificationList.map((notification) => {
                      const { message, id, getNotificationUrl,createdAt,action } = notification;
                      
                      return (
                        <tr id={`notificationsrecord${id}`} key={id}>
                          <td>
                            <div className="md-checkbox checkbox-inline">
                              <div className="btn-group recordControls">
                                <a
                                  onClick={()=>removeNotification(id)}
                                  className="btn btn-link btn-icon btn-clean delete"
                                  data-href={`/widget/notifications/delete/${id}`}
                                >
                                  <span className="svg-icon-lg svg-icon-dark-75">
                                    <svg
                                      xmlns="http://www.w3.org/2000/svg"
                                      width="24"
                                      height="24"
                                      viewBox="0 0 24 24"
                                    >
                                      <g
                                        data-name="Group 4331"
                                        transform="translate(10956 -4533)"
                                      >
                                        <rect
                                          id="Rectangle_2069"
                                          data-name="Rectangle 2069"
                                          width="24"
                                          height="24"
                                          transform="translate(-10956 4533)"
                                          fill="none"
                                          opacity="0"
                                        ></rect>
                                        <g
                                          id="Group_4324"
                                          data-name="Group 4324"
                                          transform="translate(-10952 4536)"
                                        >
                                          <path
                                            id="Path_5188"
                                            data-name="Path 5188"
                                            d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z"
                                            fill="#545454"
                                            fillRule="evenodd"
                                          ></path>
                                        </g>
                                      </g>
                                    </svg>
                                  </span>
                                </a>{" "}
                              </div>
                            </div>
                          </td>
                          <td style={{ minWidth: 200 + "px" }}>
                          {getNotificationAction(action,getNotificationUrl,message)}
                          </td>
                          <td style={{ minWidth: 130 + "px" }}>
                            <span>{moment(new Date(createdAt * 1000)).fromNow()}</span>
                          </td>
                        </tr>
                      );
                    })}
              </tbody>
            </table>
            <div className="row">
              <div className="col-md-12 text-center">
                    <span className="me-3">{contentLength*(index)+1} - {(contentLength*(index)+10)<notificationListLength?contentLength*(index)+10:notificationListLength} of {notificationListLength}</span>
                    <button type="text" className="btn btn-icon btn-sm btn-light btn-hover-danger me-2 my-1" onClick={()=>decrementIndex(index-1)}>
                      <i className="fa fa-angle-left icon-sm"></i>
                    </button>
                    <button type="text"  className="btn btn-icon btn-sm btn-light btn-hover-danger me-2 my-1" onClick={()=>incrementIndex(index+1)}>
                      <i className="fa fa-angle-right icon-sm"></i>
                    </button> 
              </div>
            </div>
          </div>:
              <div className="col-12">
                <div className="card card-custom w-100">
                  <div className="card-body">
                    <div className="portlet-body col-md-12">
                      <div className="no_connected_acc">
                        <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/products/product-empty.svg`} className="mb-10 img-fluid" />
                        <h4 className="h1 mb-4">No Notifications</h4>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
          }
        </div>
      </div>
    </div>
    </div>
  );
}
